* {
  box-sizing: border-box;
}

body {
  padding: 40px;
  min-width: 1170px;
  font-size: 14px;
  background-color: #f9f9f9;
  font-family: 'Roboto', sans-serif;
}

.main {
  width: 1040px;
  margin: 0 auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 5px;
}

img {
  width: 100%;
  max-width: 100%;
}

.line {
  display: flex;

  margin-bottom: 30px;

  &:first-child {
    margin-bottom: 60px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.cell {
  display: flex;
  flex-direction: column;
  &:first-child {
    width: 255px;
    margin-right: 30px;
  }

  &:last-child,
  &:only-child {
    flex: 1;
  }
}

.main-photo {
  height: 255px;
  background-size: 100% auto;
  background-repeat: no-repeat;
  border-radius: 5px;
}

h1 {
  font-size: 45px;
  font-weight: 500;
  margin-bottom: 10px;
}

h2 {
  font-size: 28px;
  font-weight: 500;
  margin-bottom: 10px;
}

h3 {
  font-size: 18px;
  font-weight: 500;
}

.contacts {
  margin-top: auto;

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    img {
      width: 25px;
      margin-right: 15px;
    }

    a {
      text-decoration: none;
      color: initial;
      transition: all .2s;

      &:hover {
        color: rgb(0, 0, 238);
      }
    }
  }
}

.skills-list {
  font-size: 16px;

  li {
    margin-bottom: 10px;
    padding-left: 30px;

    &::before {
      content: '-';
      margin-right: 10px;
    }
  }
}

.about {
  padding-left: 30px;
  font-size: 16px;
  line-height: 25px;
}